<script>
export default {

    props: {
        toggleModal: Function,
        releve: Boolean
    },
    methods: {
        deployModule: function (id) {
            if (id == 1) {
                let expend = document.querySelectorAll('.cours-result-years')[0].children[1].innerText;
                if (expend == "expand_more") {
                    document.querySelectorAll('.cours-result-years')[0].children[1].innerText = "chevron_right";
                    document.querySelectorAll('.deployed')[0].style.display = 'none';
                } else {
                    document.querySelectorAll('.cours-result-years')[0].children[1].innerText = "expand_more";
                    document.querySelectorAll('.deployed')[0].style.display = 'flex';
                }
            } else if (id == 2) {
                let expend = document.querySelectorAll('.cours-result-years')[1].children[1].innerText;
                if (expend == "expand_more") {
                    document.querySelectorAll('.cours-result-years')[1].children[1].innerText = "chevron_right";
                    document.querySelectorAll('.deployed')[1].style.display = 'none';
                } else {
                    document.querySelectorAll('.cours-result-years')[1].children[1].innerText = "expand_more";
                    document.querySelectorAll('.deployed')[1].style.display = 'flex';
                }
            }
        },
        deployMatter: function (id) {
            let currentMatter = document.querySelectorAll('.module')[id].children[1].innerText;
            if (currentMatter == "expand_more") {
                document.querySelectorAll('.module')[id].children[1].innerText = "chevron_right";
                document.querySelectorAll('.matter-math')[id - 4].style.display = "none";
            } else {
                document.querySelectorAll('.module')[id].children[1].innerText = "expand_more";
                document.querySelectorAll('.matter-math')[id - 4].style.display = "initial";
            }
        },
        nav: function (year, i) {
            const matterSpan = document.querySelectorAll('.title-matter')[i].innerHTML;
            this.$router.push({ path: '/cours', query: { research: '', year: year, matter: matterSpan.trim() } })
        }

    }, mounted() {

    }

}
</script>

