<template>
    <div class="header-home-container">
        <div class="button-header-container">
            <button v-if="boolMod" class="home-header-button" @click="Nav()">
                Cours
            </button>
            <button v-if="boolAdmin" class="home-header-button" @click="Nav()">Admin</button>
        </div>
        <div class="icon-header-container">
            <span class="material-icons" v-if="bool" @click="logout()">logout</span>
            <span class="material-icons">shopping_bag</span>
            <span v-if="!bool" class="material-icons" @click="onPageChanged">person</span>
        </div>
    </div>
</template>
  
<script>
import axios from 'axios';
export default {
    data() {
        return {
            bool: false,
            boolMod: false,
            boolAdmin: false
        }
    },
    methods: {
        onPageChanged: function () {
            this.$router.push({ path: '/login' })
        },
        logout: async function () {
            try {
                const response = await axios.get('/api/logout', { withCredentials: true });
                console.log('response', response.data);
            } catch (error) {
                console.error(error);
            }
            window.location.reload();
        },
        displayContent: async function () {
            try {
                await axios.get('/api/secret-route', {
                    withCredentials: true
                })
                this.bool = true;
            }
            catch {
                // c    onsole.log(err);
            }
            try {
                await axios.get('/api/mod-cours', {
                    withCredentials: true
                })
                this.bool = true;
                this.boolMod = true;
            } catch (err) {
                console.log("oui");
            }
        },
        Nav: function () {
            this.$router.push('/admin')
        }
    },
    mounted() {
        this.displayContent()
    }
}
</script>
  
<style lang="scss">
.header-home-container {
    width: 100%;
    height: 100%;
    display: flex;
    // justify-content: center;
    align-items: center;
    background-color: #141414;

    .button-header-container {
        height: 100%;
        margin-left: 20%;
        width: 60%;
        display: flex;
        justify-content: center;
        align-items: center;

        button {
            color: white;
            font-size: 0.75rem;
            background: linear-gradient(red, red) bottom / 0 .1rem no-repeat;
            transition: 0.4s;

            &:hover {
                border-radius: 10%;
                background-size: 80% .1rem;
                color: red;
            }
        }
    }

    .icon-header-container {
        height: 100%;
        width: 18%;
        display: flex;
        align-items: center;
        // color: white;
        justify-content: flex-end;
        cursor: pointer;

        .material-icons {
            clip-path: circle(50% at 50% 50%);

            margin-left: 20px;
            font-size: 1.2rem;
            height: 100%;
            width: 15%;
            color: white;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.5s;

            &:hover {
                background-color: rgba(65, 65, 65, 1);
            }
        }
    }
}
</style>