                        color: white;
                        transition: all 0.3s;
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        z-index: 1;
                        background-color: rgba(65, 65, 65, 0.5);
                        font-size: 0.6875rem;
                        font-weight: 700;

                        span {
                            transition: all 0.3s;
                        }

                        &::before {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            z-index: 1;
                            opacity: 0;
                            transition: all 0.3s;
                            border-top-width: 1px;
                            border-bottom-width: 1px;
                            border-top-style: solid;
                            border-bottom-style: solid;
                            border-top-color: rgba(228, 38, 24, 0.5);
                            border-bottom-color: rgba(228, 38, 24, 0.5);
                            transform: scale(0.1, 1);
                        }

                        &::after {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            z-index: -1;
                            transition: all 0.3s;
                        }

                        &.active::after {
                            opacity: 0;
                            transform: scale(0.1, 1);
                        }

                        &.active::before {
                            opacity: 1;
                            transform: scale(1, 1);
                        }

                        &.active {
                            color: red;
                            background-color: rgba(255, 255, 255, 0);

