                height: 7%;
                width: 25%;
                display: flex;
                flex-direction: column;
                position: absolute;
                z-index: 1;
                background-color: rgba(65, 65, 65, 0.5);
                top: 20%;
                margin-left: 5%;
                transition: height 1s, top 1s, position 1s, margin-left 1s 1s;

                .module {
                    color: red;
                    height: 10%;
                    display: flex;
                    align-items: center;
